        myDefaultKind = '';
        myDefaultElement = characterMaster[''];
        result[''] = makeDamageDetailObjArr(characterMaster[''], myTalentLevel, myDefaultKind, myDefaultElement, myStatusChangeDetailObjArr, myTalentChangeDetailObjArr, myInputCategory);

        // 
        myTalentLevel = characterInput[''];
        myDefaultKind = '';
        myDefaultElement = characterMaster[''];
        result[''] = makeDamageDetailObjArr(characterMaster[''], myTalentLevel, myDefaultKind, myDefaultElement, myStatusChangeDetailObjArr, myTalentChangeDetailObjArr, myInputCategory);

        result[''] = [] as TDamageDetailObj[];

